.recommend-container {
  position: absolute;
  top: 90px;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.article-scroller {
  height: 100%;
}
.pic {
  width: 100%;
  height: 80px;
  margin: 0;
  padding: 0;
}
.title {
  margin-top: -5px;
  letter-spacing: 1px;
  font-size: 16px;
  font-weight: bolder;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3;
  overflow: hidden;
  color: #222222;
  line-height: 16px;
  height: 50px;
}
.title2 {
  letter-spacing: 1px;
  font-size: 16px;
  font-weight: bolder;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3;
  overflow: hidden;
  color: #222222;
  line-height: 18px;
}
.item-box {
  box-shadow: 2px 2px 3px 1px #E0E0E0;
  margin: 0 10px 8px 5px;
}
.info {
  height: 30px;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  width: 95%;
}
.views,
.orgin {
  -webkit-align-self: flex-end;
          align-self: flex-end;
  font-size: 12px;
  color: #aaa;
  -webkit-box-flex: 1;
